<template>
    <!-- <v-card class="d-flex justify-center mb-6 rounded-xl"> -->
    <!-- <v-card class="d-flex justify-center mb-6 rounded-xl"> -->

    <v-container class="messageCard pa-0 pink lighten-4">
        <v-container class="py-0">
            <v-row class="justify-center">
                <v-col style="flex-grow: 0">
                    <v-img class="customAvatar"
                        src="https://iwmevhczazoydtduamuc.supabase.co/storage/v1/object/public/avatars/rick%20avatar.png">
                    </v-img>
                </v-col>
            </v-row>
            <v-row class="justify-center ma-0 dense">
                <p class="userName">{{ $route.params.userName }}</p>
            </v-row>
            <v-row class="ma-0 pa-0">
                <p class="messageLabel dense">{{caption}}</p>
            </v-row>
            <v-container fluid pa-0 class="messagePanel">
                <v-row class="ma-0 pa-0">
                    <!-- <v-text-field v-model="message" style="max-width=100" label="Message"
                        placeholder="Type your message here :)" regular>
                    </v-text-field> -->
                    <v-textarea no-resize flat solo v-model="message" name="input-2-1" background-color="transparent"
                        placeholder="Your message here"></v-textarea>
                </v-row>
            </v-container>
            <v-row class="justify-center my-0 pa-0">
                <v-btn depressed elevation="0" class="sendButton" color="#FCE4EC" @click="sendMessage"
                    :disabled="loading" :value="loading ? 'Sending' : 'Send'">
                    Send
                </v-btn>
            </v-row>
        </v-container>
    </v-container>
</template>

<style scoped>
.messageCard {
    background-color: pink lighten-4;
    color: black;
    border-radius: 20px;
}

.messagePanel {
    background-color: #FCE4EC;
    border-radius: 10px;
    width: 100%;
    padding: 10px;
}

.customAvatar {
    border-radius: 50%;
    width: 70px;
    height: 70px;
}

.userName {
    font-weight: bold;
}

.messageLabel {
    width: 100%;
    font-weight: bold;
    text-align: center;
}

.sendButton {
    font-weight: bold;
    text-transform: unset !important;
    border-radius: 10px !important;
    margin: 10px;
}
</style>

<script>
const loading = ref(false)
const message = ref('')
const userName = ref('')
const caption = ref('')

export default {
    computed: {
        currentRouteName() {
            return this.$route.params.userName;
        }
    },
    methods: {
        async sendMessage() {
            if (message.value == '') {
                alert("Please add a message")
            } else {
                try {
                    loading.value = true;
                    const { error } = await supabase.from("messages").insert({
                        // 'username': this.currentRouteName,
                        'username': this.currentRouteName,
                        'message': message.value,
                    })
                    if (error) {
                        console.log(error)
                    }
                    alert('Message sent')
                    message.value = ''
                } catch (error) {
                    if (error instanceof Error) {
                        alert(error.message)
                    }
                } finally {
                    loading.value = false;
                }
            }
        }
    },
    async mounted() {
        const { data } = await supabase.from("users").select('username, avatar, caption').eq('username', this.currentRouteName)
        this.$caption = data[0].caption
        caption.value = data[0].caption
        console.log(data[0].caption)
    }
}
</script>


<script setup>
import { ref } from 'vue'
import { supabase } from '../supabase'
</script>