<template>
    <!-- <div class="d-flex pa-2 justify-center align-self-center"> -->
    <div class="action">
        <v-row align="center" justify="center">
            <v-col>
                <v-container>
                    <!-- <v-progress-circular v-if="loading = true"
                        :size="70"
                        :width="7"
                        color="purple"
                        indeterminate/> -->
                    <MessageCard />
                </v-container>
            </v-col>
        </v-row>
    </div>

</template>

<script>
import { ref } from 'vue'
import MessageCard from '../components/MessageCard.vue'
import { supabase } from '../supabase'
import router from '../router/index'

const loading = ref(false)

export default {
    components: {
        MessageCard
    },
    computed: {
        currentRouteName() {
            return this.$route.params.userName;
        }
    },
    methods: {
        async verifyUser(userName) {
            try {
                loading.value = true
                const { data } = await supabase.from("users").select('username, avatar, caption').eq('username', userName)
                if (data == null || data.length == 0) {
                    router.push({ name: 'notFound' })
                } else {
                    loading.value = false
                    this.$caption = "tangina mo"
                    console.log(this.$caption)
                    router.replace(`/${userName}`).catch(() => { });
                }
            } catch (error) {
                alert(error)
            } finally {
                loading.value = false
            }
        }
    },
    async beforeMount() {
        await this.verifyUser(this.currentRouteName)
    },

}

</script>

<style scoped>
.action {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    margin: auto;
    max-width: 500px;
}
</style>
